import React from "react";
import cash from "../../assets/cash.svg";
import mastercard from "../../assets/mastercard.svg";
import visa from "../../assets/visa.svg";

const BillModel = ({ open, close, Order }) => {
  if (!open) {
    return null;
  }

  return (
    <>
      <div className=" fixed inset-0 backdrop-blur-sm flex justify-center items-center z-50">
        <div className="border h-5/6 mx-auto text-center rounded-lg bg-white shadow-lg w-3/6 px-3 py-4">
          <div className="py-2 h-[20%]">
            <h1 className=" text-lg font-bold">Order Details</h1>
            <div className="px-4 py-2">
              <ul className=" flex justify-between text-sm text-gray-400 items-center">
                <li>Order ID - {Order._id}</li>
                <li>Table - {Order.table.Tablename}</li>
              </ul>
              <ul className=" flex justify-between text-gray-400 text-sm items-center">
                <li>Customer - {Order.customername}</li>
                <li>Date - {new Date(Order.Date).toDateString()}</li>
              </ul>
            </div>
          </div>
          <div className=" px-4 h-[70%] overflow-auto">
            <table className="w-full table-auto">
              <thead className=" text-sm font-semibold text-gray-400 border-b-2 border-slate-300 border-double ">
                <tr>
                  <th className="text-start  py-3">ITEM NAME</th>
                  <th className="text-center py-3">QTY</th>
                  <th className="text-center py-3">PRICE</th>
                  <th className="text-center py-3">SUB TOTAL</th>
                </tr>
              </thead>
              <tbody className=" text-gray-800">
                {Order.items.map((ord, index) => {
                  return (
                    <tr
                      key={index}
                      className="border-b border-slate-300 table-row"
                    >
                      <td
                        className="text-start py-3
                    "
                      >
                        {ord.item.item}
                      </td>
                      <td
                        className="text-center py-3
                    "
                      >
                        ${ord.quantity}
                      </td>
                      <td
                        className="text-center py-3
                    "
                      >
                        ${ord.item.price}
                      </td>
                      <td
                        className="text-center py-3
                    "
                      >
                        ${ord.quantity * ord.item.price}
                      </td>
                    </tr>
                  );
                })}
              </tbody>

              <tfoot>
                <tr>
                  <td colSpan={3} className=" text-end text-gray-400">
                    SUBTOTAL
                  </td>
                  <td colSpan={3} className=" text-center">
                    $ {Order.totalamount}
                  </td>
                </tr>
                <tr>
                  <td colSpan={3} className=" text-end text-gray-400">
                    TAX
                  </td>
                  <td colSpan={3} className=" text-center">
                    $ 0.00
                  </td>
                </tr>
                <tr>
                  <td
                    colSpan={3}
                    className=" text-end text-orange-500 text-md font-semibold"
                  >
                    BILL AMOUNT
                  </td>
                  <td
                    colSpan={3}
                    className=" text-center  text-orange-500 text-md font-semibold"
                  >
                    $ {Order.totalamount}
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
          <div className=" border-t border-slate-300 flex justify-between py-2 px-4 h-[20%]">
            <div>
              {Order.paid && (
                <>
                  <h6 className=" text-gray-400 text-sm">Paymode</h6>
                  {Order.paymentMode === "cash" && (
                    <img src={cash} alt="cash" className="w-10" />
                  )}
                  {Order.paymentMode === "mastercard" && (
                    <img src={mastercard} alt="mastercard" className="w-10" />
                  )}
                  {Order.paymentMode === "visa" && (
                    <img src={visa} alt="visa" className="w-10" />
                  )}{" "}
                </>
              )}
            </div>
            <div>
              <button
                onClick={() => close()}
                className=" bg-orange-500 rounded-lg text-white w-16 h-8 hover:bg-orange-400"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default BillModel;
