import React, { useEffect, useState } from "react";
import {
  State,
  getAllOrders,
  orderState,
} from "../../redux/slices/OrdersSlice";
import { useDispatch, useSelector } from "react-redux";
import Drawer from "../Layout/Drawer";
import BillModel from "../Layout/BillModel";
import LineSpinner from "../customLoader/LineSpinner";

const Orders = () => {
  const placedOrders = useSelector(orderState);
  const { status } = useSelector(State);
  const [drawer, setDrawer] = useState(false);
  const [bill, setBill] = useState(false);
  const [order, setOrder] = useState({});
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getAllOrders());
  }, []);
  const drawerHandler = (obj) => {
    setOrder(obj);
    setDrawer(true);
  };

  const drawerCloseHandler = () => {
    setOrder({});
    setDrawer(false);
  };
  const billHandler = (obj) => {
    setOrder(obj);
    setBill(true);
  };

  const billCloseHandler = () => {
    setOrder({});
    setBill(false);
  };

  return (
    <>
      {status === "loading" && <LineSpinner />}
      {drawer && (
        <Drawer open={drawer} close={drawerCloseHandler} Order={order} />
      )}
      {bill && <BillModel open={bill} close={billCloseHandler} Order={order} />}
      <div className=" bg-slate-100 px-4 py-3 overflow-auto h-[calc(100vh-70px)] ">
        <div className=" grid grid-col-1 sm:grid-col-2 md:grid-cols-3 lg:grid-col-4 gap-2">
          {placedOrders.length === 0 && (
            <p className=" text-slate-500 text-center font-medium text-xl">
              No orders Found...
            </p>
          )}
          {placedOrders.map((order) => {
            return (
              <div
                key={order._id}
                className=" bg-white p-4 rounded-lg shadow-lg cursor-pointer"
                onClick={
                  order.paid
                    ? () => billHandler(order)
                    : () => drawerHandler(order)
                }
              >
                <div className=" border-b border-slate-200 flex justify-between pb-1">
                  <div className=" text-gray-600 font-semibold">
                    {order.table.Tablename}
                  </div>
                  <div
                    className={
                      order.paid
                        ? "rounded-md bg-green-400 text-white px-2"
                        : "rounded-md bg-yellow-400 text-white px-2"
                    }
                  >
                    {order.paid ? "Paid" : "Pending"}
                  </div>
                </div>
                <div className=" w-full border-b border-slate-200 pb-1">
                  <table className="text-slate-400">
                    <tbody>
                      <tr>
                        <td>Customer Name - </td>
                        <td> {order.customername}</td>
                      </tr>
                      <tr>
                        <td>Order Date</td>
                        <td>{new Date(order.Date).toLocaleString()}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <div className=" border-b border-slate-200 flex justify-between py-1">
                  <div className=" text-gray-600">Amount</div>
                  <div className="text-orange-500 font-bold px-2">
                    {order.totalamount}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Orders;
