import React, { useState } from "react";

const Calculator = ({ amount, setAmount }) => {
  //   const [amount, setAmount] = useState("");
  const [waitingForNext, setWaitingForNext] = useState(false);
  const calculate = (inputDigit) => {
    if (amount.length > 11) {
      return null;
    }
    if (waitingForNext) {
      setAmount(String(parseFloat(amount) + Number(inputDigit)));
      setWaitingForNext(false);
    } else {
      setAmount(amount + inputDigit);
    }
  };

  const removeLastDigit = () => {
    setAmount(amount.slice(0, -1) || "");
  };

  return (
    <>
      <div>
        <h6 className="text-md font-semibold">Pay</h6>
        <div className="py-2">
          <input
            type="text"
            className="w-full focus:outline-none px-10 py-3 md:px-16 border rounded-2xl border-gray-400 focus:border-orange-500
              "
            readOnly={true}
            maxLength={12}
            value={amount}
            placeholder="Amount"
          />
        </div>
      </div>
      <div className="grid grid-cols-4 text-center gap-2 items-center">
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(1)}
        >
          1
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(2)}
        >
          2
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(3)}
        >
          3
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold text-blue-500 cursor-pointer"
          onClick={() => calculate(10)}
        >
          10
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(4)}
        >
          4
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(5)}
        >
          5
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(6)}
        >
          6
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold text-blue-500 cursor-pointer"
          onClick={() => calculate(20)}
        >
          20
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(7)}
        >
          7
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(8)}
        >
          8
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(9)}
        >
          9
        </button>
        <button
          className="w-20 h-12 rounded-full bg-orange-200 text-orange-500  font-semibold text-center cursor-pointer"
          onClick={removeLastDigit}
        >
          Back
        </button>
        <button
          className="w-20 h-12 rounded-full bg-orange-200 text-orange-500 font-semibold cursor-pointer"
          onClick={() => setAmount("")}
        >
          C
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => calculate(0)}
        >
          0
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => setAmount((state) => state + ".")}
        >
          .
        </button>
        <button
          className="w-20 h-12 rounded-full bg-blue-200 font-semibold cursor-pointer"
          onClick={() => setWaitingForNext(true)}
        >
          Add
        </button>
      </div>
    </>
  );
};

export default Calculator;
