import React, { useState, useEffect } from "react";
import { FaWindowClose } from "react-icons/fa";
import cash from "../../assets/cash.svg";
import mastercard from "../../assets/mastercard.svg";
import visa from "../../assets/visa.svg";
import Calculator from "./Calculator";
import { IoReceiptOutline } from "react-icons/io5";
import { TiTick } from "react-icons/ti";
import { useDispatch, useSelector } from "react-redux";
import { updateOrder, State, cleanup } from "../../redux/slices/OrdersSlice";
import BillModel from "../Layout/BillModel";
import { Toaster, toast } from "react-hot-toast";
import LineSpinner from "../customLoader/LineSpinner";

const Drawer = ({ open, close, Order }) => {
  const [payment, setPayment] = useState("");
  const [amount, setAmount] = useState("");
  const [bill, setBill] = useState(false);
  const [paid, setPaid] = useState(Order.paid);
  const dispatch = useDispatch();
  const { status, code } = useSelector(State);

  useEffect(() => {
    if (code === 200) {
      if (status !== "loading" && status !== "Success") {
        if (status === "Payment Success") {
          toast.success(status);
        }
      }
    } else if (code === 500 || code === 404) {
      toast.error(status);
    }

    return () => dispatch(cleanup());
  }, [status, code]);

  const className = open
    ? " relative right-0 w-2/6 bg-white transform translate-x-0 transition ease-in-out duration-1000"
    : " relative right-0 w-2/6 bg-white transform translate-x-full transition ease-in-out duration-100";

  const onClose = () => {
    close();
  };

  const paymentHandler = () => {
    if (!payment || payment === "") {
      return toast.error("select payment");
    }
    if (parseFloat(Order.totalamount) !== parseFloat(amount)) {
      return toast.error("Invalid Amount");
    }

    let obj = { paid: true, paymentMode: payment, id: Order.id };
    dispatch(updateOrder(obj));
    setPaid(true);
  };
  const billHandler = (obj) => {
    setBill(true);
  };

  const billCloseHandler = () => {
    setBill(false);
  };

  if (!open) {
    return null;
  }
  return (
    <>
      {status === "loading" && <LineSpinner />}
      <Toaster />

      {bill && <BillModel open={bill} close={billCloseHandler} Order={Order} />}
      <div className="inset-0 fixed z-40 backdrop-brightness-75 flex justify-end">
        <div className={className}>
          <div className="h-full mx-auto px-6 py-5 flex flex-col justify-evenly gap-3">
            <div className="">
              <div className="flex flex-col md:flex-row border-b border-slate-300  justify-between ">
                <div>
                  <h3 className=" text-lg font-semibold">Order Payment</h3>
                  <h6 className="text-base text-gray-300">
                    OrderId: {Order.id}
                  </h6>
                </div>

                <FaWindowClose onClick={onClose} className=" cursor-pointer" />
              </div>
              <div className=" px-5 py-2 ">
                <div className=" bg-slate-100 py-4 rounded-md">
                  <div className=" flex flex-col justify-center items-center md:flex-row md:justify-around ">
                    <p className=" text-gray-500 text-center">Total Amount</p>
                    <p className=" text-orange-500">${Order.totalamount}</p>
                  </div>
                </div>
              </div>
              <div className=" px-5 py-2">
                <h6 className="text-md font-semibold">Payment Method</h6>
                <div className=" flex justify-around items-center pt-2">
                  <button
                    className={
                      payment === "cash"
                        ? " rounded border  border-orange-500 cursor-pointer"
                        : " rounded border  border-slate-300 cursor-pointer  hover:border-orange-500"
                    }
                    onClick={() => setPayment("cash")}
                  >
                    <img src={cash} alt="cash" />
                  </button>
                  <button
                    className={
                      payment === "mastercard"
                        ? " rounded border  border-orange-500 cursor-pointer"
                        : " rounded border  border-slate-300 cursor-pointer  hover:border-orange-500"
                    }
                    onClick={() => setPayment("mastercard")}
                  >
                    <img src={mastercard} alt="mastercard" />
                  </button>
                  <button
                    className={
                      payment === "visa"
                        ? " rounded border  border-orange-500 cursor-pointer"
                        : " rounded border  border-slate-300 cursor-pointer  hover:border-orange-500"
                    }
                    onClick={() => setPayment("visa")}
                  >
                    <img src={visa} alt="visa" />
                  </button>
                </div>
              </div>
            </div>
            <div className="px-5">
              <Calculator amount={amount} setAmount={setAmount} />
            </div>
            <div className="px-5">
              <div className="flex justify-between">
                <button
                  className=" bg-black text-white rounded-2xl  flex flex-col justify-center items-center w-32 h-14"
                  onClick={billHandler}
                >
                  <IoReceiptOutline />
                  <span>View</span>
                </button>

                <button
                  className=" bg-orange-500 text-white rounded-2xl  flex flex-col justify-center items-center w-32 h-14 "
                  onClick={paymentHandler}
                  disabled={paid}
                >
                  <TiTick />
                  <span>Done</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Drawer;
